import { Box, Button, Flex, Heading, Link, Text } from "@chakra-ui/react";
import React from "react";
import styles from "./Banner.module.css";
function LeftBanner() {
  return (
    <Flex className={styles.leftBanner}  flexDirection="column" alignItems="flex-start" justifyContent={"center"} >
      <Heading className={styles.heading}>Hello,</Heading>
      <Heading className={styles.nameHeading}>
        I'm <span className={styles.name}>Golla Mahesh Kumar</span>
      </Heading>
      <Text className={styles.role}>and I'm Web Developer</Text>
      <Link download={""} href=""  className={styles.downloadBtn} size={"md"}>DOWNLOAD CV</Link>
    </Flex>
  );
}

export default LeftBanner;
