import { HamburgerIcon } from "@chakra-ui/icons";
import {
  Box,
  Button,
  Drawer,
  DrawerBody,
  DrawerCloseButton,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  Heading,
  Link,
  Spacer,
  useDisclosure,
} from "@chakra-ui/react";
import React, { useState } from "react";
import NavLogo from "./NavLogo";
import styles from "./Navbar.module.css";
import { useEffect } from "react";
function NavLinks() {
  const [size, setSize] = React.useState("full");
  let [width, setWidth] = useState();
  const { isOpen, onOpen, onClose } = useDisclosure();
  useEffect(() => {
    setWidth(window.innerWidth);
  }, []);

  const handleClick = () => {
    onOpen();
  };

  window.addEventListener("resize", () => {
    setWidth(window.innerWidth);
  });
  return width > 768 ? (
    <Box display={"flex"} gap={"24px"}>
      <Link className={styles.navLink} href="#about">
        About
      </Link>
      <Link className={styles.navLink} href="#projects">
        Projects
      </Link>
      <Link className={styles.navLink} href="#skills">
        Skills
      </Link>
      <Link className={styles.navLink} href="#contact">
        Contact Me
      </Link>
    </Box>
  ) : (
    <>
      <Button onClick={() => handleClick(size)} key={size} m={4}>
        {<HamburgerIcon />}
      </Button>

      <Drawer placement="top" onClose={onClose} isOpen={isOpen} size={size}>
        <DrawerOverlay />
        <DrawerContent className={styles.menu__overlay}>
          <DrawerCloseButton />
          <DrawerHeader>
            <NavLogo className={styles.DrawerNav} />
          </DrawerHeader>
          <hr />
          <DrawerBody>
            <Box textAlign={"center"}>
              <Box>
                <Link
                  className={styles.drawer__link}
                  href="#about"
                  blockScrollOnMount={true}
                >
                  <Heading>About</Heading>
                </Link>
              </Box>
              <Box>
                <Link className={styles.drawer__link}>
                  {" "}
                  <Heading>Projects</Heading>{" "}
                </Link>
              </Box>
              <Box>
                <Link className={styles.drawer__link}>
                  <Heading>Skills</Heading>
                </Link>
              </Box>
              <Box>
                <Link className={styles.drawer__link}>
                  <Heading>Contact</Heading>
                </Link>
              </Box>
            </Box>
          </DrawerBody>
        </DrawerContent>
      </Drawer>
    </>
  );
}

export default NavLinks;
